*, :before, :after {
  box-sizing: border-box;
  font-family: Gotham Rounded, sans-serif;
  font-weight: normal;
}

body {
  height: 100vh;
  background: linear-gradient(to right, #810383, #7b4b8776);
}

#topicWrapper {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: .5rem 0;
  display: flex;
}

form textarea {
  resize: none;
  box-shadow: 2px 2px 2px #fff;
}

input {
  box-shadow: 2px 2px 2px #fff;
}

#ShowsTag {
  width: 25%;
  height: 40%;
  padding: 1.5rem, 0rem;
  margin: 1.5rem, 0rem;
  background-color: #a40dc6;
}

#musicDiv, #showsDiv, #gamesDiv {
  width: 15%;
  height: 15%;
  background-color: #a40dc6;
  margin: 1.5rem 0;
  padding: 1.5rem;
}

a {
  color: #fff;
  text-decoration: none;
}

div button {
  width: 1.75rem;
  height: 1.15rem;
  padding: .25rem;
}

.chat-styling {
  color: #fff;
  text-shadow: .8px .8px .8px #ca40d4;
  width: 25%;
  background-color: #a40dc6;
  border-radius: 15px;
  margin: 1.5rem;
  padding: 1rem;
  display: flex;
  box-shadow: 4.4px 4.4px 4.4px #d341f0;
}

.chat-styling button {
  margin-left: 2rem;
  margin-right: 0;
}

button {
  box-shadow: 2px 2px 2px #fff;
}

form {
  width: 100%;
  height: 14rem;
  margin-bottom: 0;
  padding-top: 1.2rem;
  padding-bottom: 1.5rem;
  position: absolute;
  bottom: 2.2rem;
}

#messagesShows {
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow-y: scroll;
}

